<template>
    <div class="order-comment">
        <div class="comment-content">
            <div class="comment-item" v-for="(item,index) in commentOrderInfo">
                <div class="top-box">
                    <img :src="item.smallImg" alt="" class="img left" />
                    <div class="text-box left">
                        <p class="order-info">{{item.chrName}}</p>
                        <div class="block">
                            <span class="demonstration">{{$L('综合评分')}}</span>
                            <el-rate v-model="item.meanScore" disabled></el-rate>
                        </div>
                    </div>
                </div>
                <el-input :rows="5" maxlength="500" :placeholder="$L('写下您的评价')+'...'" resize="none" type="textarea" v-model="item.prodText" class="desc" @change="inputChange(item.prodId)"></el-input>
                <p style="text-align: right"><span class="desc-num gary">{{item.prodText ? item.prodText.length:0}}/500</span></p>
                <uploadPic @UpLoadPic="getImg" v-bind:id="item.prodId"></uploadPic>
            </div>
            <div class="comment-bottom">
                <div class="title">{{ $L('网站动态评分') }}</div>
                <div class="rate-box">
                    <p class="p"><span class="red">*</span>{{$L(' 宝贝与描述相符')}}<el-rate v-model="product"></el-rate></p>
                    <p class="p"><span class="red">*</span>{{$L(' 卖家的服务态度')}}<el-rate v-model="service"></el-rate></p>
                    <p class="p"><span class="red">*</span>{{$L(' 物流服务的质量')}}<el-rate v-model="logistics"></el-rate></p>
                </div>
                <el-button size="larger" type="primary" @click="submitFunc">{{ $L('评价详情') }}</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import uploadPic from "../../../components/order/uploadPic";
    export default {
        name: "orderComment",
        components:{
            uploadPic
        },
        data () {
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                commentOrderInfo:null,
                product:5,
                service:5,
                logistics:5,
                score:[],
                addComment: null,
                evaluate:{},
                imgList:[],
                Eval:{
                    Product: [],
                    Service: []
                },
                prodText:'',
                idSpecPrice: [],
                idProduct: [],
            }
        },
        created() {
            let id = 0;
            if (this.$route.params.hasOwnProperty('id')) {
                id = this.$route.params.id;
                id = parseInt(id);
            }
            if (!isNaN(id) && id) {
                this.getData(id);
            } else {
                this.showError();
            }
        },
        methods:{
            backFunc() {
                this.$router.push({name:'orderList',params:{type:'all'}});
            },
            showError() {
                this.$alert(this.$L('订单不存在或已删除'), this.$L('参数有误'), {
                    confirmButtonText: this.$L('返回'),
                    type: 'error',
                }).then(() => {
                    this.backFunc();
                });
            },
            getData(id) {
                const _self = this;
                _self.loading = true;
                _self.$api('commentOrderInfo', {orderId: id}, function (reData) {
                    if (reData.flag === 1) {
                        _self.commentOrderInfo = reData.data.commentList;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('重试'),
                            cancelButtonText: _self.$L('返回'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData(id);
                        }).catch(action => {
                            _self.backFunc();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('重试'),
                        cancelButtonText: _self.$L('返回'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData(id);
                    }).catch(action => {
                        _self.backFunc();
                    });
                });
            },
            //获取子组件的图片
            getImg(data,id) {
                this.imgList[id] = {
                    'fileList': data
                };
            },
            inputChange(prodId) {
                for (let i in this.commentOrderInfo) {
                    if (this.commentOrderInfo[i]['prodId'] == prodId) {
                        this.Eval.Product[prodId] = this.commentOrderInfo[i]['prodText'];
                        this.Eval.Service[prodId] = this.$L("非常好");
                    }
                }

            },
            //删除图片
            handleRemove(index){
                this.uploadList.splice(index, 1);
                this.showImgArr.splice(index, 1);
                this.uploadLen = this.showImgArr.length;
            },
            //上传图片
            upImgFunc(event) {
                const _self = this;
                let files = event.target.files;
                let oldLen = _self.uploadLen;
                let len = files.length+oldLen;
                if(len < _self.maxNum){
                    _self.$message({
                        showClose: true,
                        message: '最多可上传'+_self.maxNum+'张，您还可以上传'+(_self.maxNum - len)+'张',
                        type: 'success'
                    });
                    // return false;
                }
                for (let i=0; i < files.length; i++) {
                    let imgUrl = null;
                    let size = Math.floor(files[i].size / 1024);
                    let type = files[i].type.substring(6);
                    if (size > 2*1024*1024) {
                        _self.$Message({
                            showClose: true,
                            message: '请选择2M以内的图片！',
                            type: 'warning'
                        });
                        return false;
                    }
                    if(!(type === 'png' || type === 'jpeg' || type === 'jpg')){
                        _self.$Message({
                            showClose: true,
                            message: '请选择png、jpeg、jpg的图片格式！',
                            type: 'warning'
                        });
                        return false;
                    }
                    _self.uploadList.push({
                        'file':files[i]
                    });
                    _self.uploadLen++;
                    let reader = new FileReader();
                    reader.readAsDataURL(files[i]);
                    reader.onload = function(evt){
                        imgUrl = evt.target.result;
                        _self.showImgArr.push(imgUrl);
                    }
                }
                // _self.$emit('UpLoadPic',_self.uploadList);
            },
            submitFunc(){

                const _self = this;
                _self.loading = true;
                for(let i in _self.commentOrderInfo){
                    _self.idSpecPrice[_self.commentOrderInfo[i]['prodId']] = _self.commentOrderInfo[i]['goodsSpecId'];
                    _self.idProduct[i] = _self.commentOrderInfo[i]['prodId'];
                    if(!_self.Eval['Product'][_self.commentOrderInfo[i]['prodId']]) {
                        _self.$message.error(_self.$L("请完善评论信息"));
                        return;
                    }

                }
                let id = _self.$route.params.id;
                _self.imgList[id] = {
                    'fileList':_self.uploadList
                };
                let arr = {
                    IDOrder:id,
                    Score: {
                        'Desc':_self.product,
                        'Service':_self.service,
                        'KuaiDi':_self.logistics
                    },
                    intShow: 1,
                    Eval : _self.Eval,
                    IDSpecPrice: _self.idSpecPrice,
                    IDProduct: _self.idProduct,
                    'CommentsPic': {
                        fileList: _self.imgList
                    },

                };

                let postData = _self.$objToFormData(arr);
                _self.$api('addComment', postData, function (reData) {
                    if (reData.flag === 1) {

                        _self.$message({
                            type: 'success',
                            message: _self.$L('提交成功!')
                        });
                        _self.$router.push({name: 'checkComment'});
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.backFunc();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.backFunc();
                    });
                }, true);

            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/icon/iconfont.css";
    @import "../../../assets/css/common/lib.scss";
    .order-comment{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;
        .comment-content{
            font-size: 12px;
            .comment-item{
                background-color: #fff;
                padding: 20px 10px 10px;
                overflow: hidden;
                margin-bottom: 10px;
                .top-box{
                    overflow: hidden;
                    margin-bottom: 20px;
                    .img{
                        height: 80px;
                        width: 80px;
                    }
                    .text-box{
                        width: calc(100% - 90px);
                        margin-left: 10px;
                        .order-info{
                            word-wrap:break-word
                        }
                        .block{
                            margin-top: 10px;
                            .el-rate{
                                display: inline-block;
                                margin-left: 20px;
                                vertical-align: middle;
                            }
                        }
                    }
                }
                .info{
                    text-align: center;
                    .order-info{
                        margin-top: 5px;
                        @extend .text-one-line;
                    }
                    .img{
                        width: 100px;
                        height: 100px;
                    }
                    .block{
                        .demonstration{
                            padding: 15px 0 5px;
                            display: inline-block;
                        }
                    }
                }
                .desc{
                    font-size: 14px;
                }
                .desc-num{
                    vertical-align: bottom;
                    line-height: 1;
                    padding-left: 2%;
                }
                .up-load-pic{
                    display: inline-block;
                    margin: 10px 0;
                    .img-list{
                        overflow: hidden;
                        .load-img{
                            width: 55px;
                            height: 55px;
                            line-height: 55px;
                            float: left;
                            text-align: center;
                            position: relative;
                            overflow: hidden;
                            margin-right: 10px;
                            &.up-load-img{
                                background-color: #F0F1F5;
                                text-align: center;
                                margin-right: 15px;
                            }
                            &:hover .upload-list-cover{
                                display: block;
                            }
                            .img{
                                width: 100%;
                                height: auto;
                            }
                            .upload-list-cover{
                                display: none;
                                position: absolute;
                                top: 0;
                                bottom: 0;
                                left: 0;
                                right: 0;
                                background: rgba(0,0,0,.6);
                                .bbx-font{
                                    color: #fff;
                                    font-size: 16px;
                                    cursor: pointer;
                                }
                            }
                            .upload{
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 100%;
                                height: 100%;
                                opacity: 0;
                                cursor: pointer;
                            }
                        }
                    }
                }
                .img-num{
                    line-height: 1;
                }
            }
            .comment-bottom{
                background-color: #fff;
                overflow: hidden;
                padding: 20px 10px;
                .title{
                    font-size: 14px;
                    margin-bottom: 10px;
                }
                .p{
                    margin-bottom: 10px;
                    .el-rate{
                        display: inline-block;
                        vertical-align: bottom;
                        margin-left: 10px;
                    }
                }
                .el-button{
                    width: 100%;
                    font-size: 12px;
                    margin-top: 50px;
                }
            }
        }
        .red{
            color: #FF0000;
        }
        .gary{
            color: #999999;
        }
        .left{
            float: left;
        }
        .right{
            float: right;
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>